import React, { useCallback, useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import LayoutPage from "../Layout";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { saveAs } from "file-saver";
import axios from "axios";
import { toast } from "react-toastify";
import DataTable from "react-data-table-component";

function List() {
  const location = useLocation();
  const [samplePdf, setSamplePdf] = useState("");
  const [loading, setLoading] = useState(false);
  const [categoryData, setCategoryData] = useState("");
  const [totalRows, setTotalRows] = useState(0);
  const [pageNumber, setPageNumber] = useState(1);
   const [perPage, setperPage] = useState(10);

  const navigate = useNavigate();


  const columns = [
    {
      name: "Championship Name",
      selector: (row) => row.championshipName,
    },
    {
      name: "createdAt",
      selector: (row) => row.createdAt,
    },
    {
      name: "updatedAt",
      selector: (row) => row.updatedAt,
    },
    {
      name: "Action",
      selector: (row) => row.id,
      cell: (row) => (
        <div>
          {/* <Link to={`/advertise/edit/${row._id}`}> */}
            <i
              title="Edit"
              style={{ cursor: "pointer" }}
              className="fa fa-pencil text-success"
            ></i>
          {/* </Link> */}
          &nbsp;&nbsp;
          {/* <i
            title="Delete"
            style={{ cursor: "pointer" }}
            className="fa fa-trash text-danger"
            onClick={() => handleDeleteConfirm(row, "Delete")}
          ></i> */}
        </div>
      ),
      center: true,
    },
  ];

  const getCategoryData = useCallback(async () => {
    try {
      setLoading(true);
      const res = await axios.get(
        `/game/getAllChampionship?pageNumber=${pageNumber}&pageSize=${perPage}&search=`
      )
      console.log("cham -----", res.data.data)
      setCategoryData(res.data.data.result);
      setTotalRows(res);
      setLoading(false);
    } catch (error) {
      console.log("error", error);
    }
  }, [pageNumber, perPage]);



  const handlePageChange = (page) => {
    setPageNumber(page);
  };
   const handlePerRowsChange = async (newPerPage, page) => {
     setPageNumber(page);
     setperPage(newPerPage);
   };

  useEffect(() => {
    getCategoryData();
    if (location.state) {
      let msg = location.state.message;
      window.history.replaceState({}, document.title);
      navigate(location.pathname, { replace: true });
      toast(msg, {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        type: "success",
      });
    }
  }, [getCategoryData, pageNumber, perPage, location]);

  return (
    <>
      <Helmet>
        <title>Championship Management</title>
      </Helmet>
      <LayoutPage>
        <div className="row">
          <div className="col-12">
            <div className="card my-4">
              <div className="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
                <div className="bg-gradient-primary shadow-primary border-radius-lg pt-4 pb-3">
                  <h6 className="text-white text-capitalize ps-3 custom-card-heading">
                    Championship
                  </h6>
                  {/* <Link
                    to="/game/addgame"
                    title="Add New"
                    className="btn btn-rounded btn-icon btn-primary custom-add-new-button"
                  >
                    <i className="fa fa-plus"></i>
                  </Link> */}
                </div>
              </div>
              <div className="card-body px-0 pb-2">
                <div className="p-4">
                  <div className="input-group input-group-dynamic">
                    &nbsp; &nbsp;
                  </div>
                  {/* <h1>Sport Category</h1> */}
                  <div className="table-responsive p-0">
                    <DataTable
                      columns={columns}
                      data={categoryData}
                      progressPending={loading}
                      pagination
                      paginationServer
                      paginationTotalRows={totalRows}
                      onChangeRowsPerPage={handlePerRowsChange}
                      onChangePage={handlePageChange}
                      sortServer
                      // onSort={handleSort}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutPage>
    </>
  );
}

export default List;
